.wrapper-main-rund-first,
.wrapper-main-rund-second,
.wrapper-main-rund-third {
    position: absolute;
    width: 44px;
    height: 44px;
    font-size: 25px;
    font-weight: 900;
    color: #fff;
    background: $bluebuttoncolor;
    box-shadow: $bluebuttonshadow;
    border: none;
    box-sizing: border-box;
    border-radius: 100%;
}
.wrapper-main-rund-first::after,
.wrapper-main-rund-second::after,
.wrapper-main-rund-third::after {
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    content: '';
    transform: translate(-50%, -50%);
    animation-name: bullet-waves;
    animation-duration: 2s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
    opacity: 0;
    border: 1px solid #fff;
    border-radius: 100%;
}
.wrapper-main-rund-first::after{
    animation-delay: 1s;
}
.wrapper-main-rund-second::after{
    animation-delay: 1.5s;
}
.wrapper-main-rund-third::after{
    animation-delay: 2s;
}
.wrapper-main-rund-first {
    top: 101px;
    right: 394px;
}
.wrapper-main-rund-second {
    top: 272px;
    right: 206px;
}
.wrapper-main-rund-third {
    top: 405px;
    right: 426px;
}
@keyframes bullet-waves
{
    0%
    {
        width: 100%;
        height: 100%;
        opacity: 1;
    }
    50%
    {
        width: 250%;
        height: 250%;
        opacity: 0;
    }
    100%
    {
        width: 100%;
        height: 100%;
    }
}
