/* slider!! */ 
.wrapper-constr-stages-slider .all {
    padding-top: 51px;
    #slider {
        position: relative;
    }
    #slides article { /*     */
        width: 16.666%;
        float: left;
    }
    #slides .image { /*     */
        width: 600%;
        line-height: 0;
    }
    #overflow { /* ,      */
        width: 100%;
        overflow: hidden;
    }	
    #desktop:checked ~ #slider { /*  */
        max-width: 976px; /* */
    }
    /*      */
    /*  1-6 ,         id */
    #switch1:checked ~ #controls label:nth-child(6), 
    #switch2:checked ~ #controls label:nth-child(1),
    #switch3:checked ~ #controls label:nth-child(2),
    #switch4:checked ~ #controls label:nth-child(3),
    #switch5:checked ~ #controls label:nth-child(4),
    #switch6:checked ~ #controls label:nth-child(5) {
        background: $orangebuttoncolor; 
        float: left;
        margin: -30px 0 0 -66px; /* */
        display: block;
        height: 65px;
        width: 65px;
        border-bottom: solid 5px $orangebuttonborder;
        border-radius: 50%;
    }
    /*      */
    #switch1:checked ~ #controls label:nth-child(2), 
    #switch2:checked ~ #controls label:nth-child(3),
    #switch3:checked ~ #controls label:nth-child(4),
    #switch4:checked ~ #controls label:nth-child(5),
    #switch5:checked ~ #controls label:nth-child(6),
    #switch6:checked ~ #controls label:nth-child(1) {
        background: $orangebuttoncolor; 
        float: right;
        margin: -30px -65px 0 0; 
        display: block;
        height: 65px;
        width: 65px;
        border-bottom: solid 5px $orangebuttonborder;
        border-radius: 50%;
    }
    #controls span {
        font-family: GirloyExtraBold;
        position: absolute;
        font-size: 35px;
        color: white;
        margin-top: 16px;
        margin-left: 23px;
    }
    label, a { /*      -  */
        cursor: pointer;
    }
    input { /*   */
        display: none;
    }
    /*    */
    #switch1:checked ~ #slides .image {
        margin-left: 0;
    }
    #switch2:checked ~ #slides .image {
        margin-left: -100%;
    }
    #switch3:checked ~ #slides .image {
        margin-left: -200%;
    }
    #switch4:checked ~ #slides .image {
        margin-left: -300%;
    }
    #switch5:checked ~ #slides .image {
        margin-left: -400%;
    }
    #switch6:checked ~ #slides .image {
        margin-left: -500%;
    }
    #controls { /*    */
        margin: -25% 0 0 0;
        width: 100%;
        height: 50px;
    }
    // #active label{ /*  */
    //     border-radius: 10px; /* */
    //     display: inline-block; /*  */
    //     width: 15px;
    //     height: 15px;
    //     background: #bbb;
    // }
    #active { /*   */
        margin: 23% 0 0;
        text-align: center;
    }
    // #active label:hover { /*   */
    //     background: #76c8ff;
    // }
    /*     */
    // #switch1:checked ~ #active label:nth-child(1),
    // #switch2:checked ~ #active label:nth-child(2),
    // #switch3:checked ~ #active label:nth-child(3),
    // #switch4:checked ~ #active label:nth-child(4),
    // #switch5:checked ~ #active label:nth-child(5),
    // #switch6:checked ~ #active label:nth-child(6) {
    //     background: #18a3dd;
    // }
    #slides .image{ /*  */
        transition: all 800ms cubic-bezier(0.770, 0.000, 0.175, 1.000);
    }
    #controls label:hover{ /*   */
        opacity: 1;
    }
    #controls label{ /*    */
        opacity: 0.8;
        transition: opacity 0.2s ease-out;
    }
    .first,
    .second,
    .third,
    .fourth,
    .fifth,
    .sixth {
        position: relative;
        height: 512px;
        width: 100%;
        overflow: hidden;
    }
    .first img,
    .second img,
    .third img,
    .fourth img,
    .fifth img,
    .sixth img {
        border-radius: 20px;
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 1;
