        padding-top: 42px;
        padding-bottom: 103px;
        z-index: 10;
    }
    .slider__wrapper {
        display: flex;
        transition: transform 0.6s ease;
    }
    .slider__item {
        margin-right: 90px;
        flex: 0 0 50%;
        max-width: 335px;
        height: 340px;
        text-align: center;
        .element-container {
            margin: 0 auto;
            position: relative;
            background: $sectionbackgroundcolor;
            height: 290px;
            width: 275px;
            padding: 0 30px;
            margin-top: 50px;
            border-radius: 20px;
