                    opacity: 0.5;
                }
            }
            p {
                font-family: GirloyMedium;
                font-size: 15px;
                line-height: 25px;
            }
        }
        img {
            position: absolute;
            left: 45%;
            top: -35px;
            margin: 0 auto;
            display: inline-block;
            width: fit-content;
        }
    }
    .slider__control {
        position: absolute;
        top: 50%;
        display: none;
        align-items: center;
        justify-content: center;
        width: 40px;
        color: #fff;
        text-align: center;
        opacity: 0.8;
        height: 50px;
        transform: translateY(-30%);
        background: rgba(0, 0, 0, .5);
    }
    .slider__control_show {
        display: flex;
    }
    .slider__control {
        cursor: pointer;
    }
    .slider__control:hover,
    .slider__control:focus {
        color: #fff;
        text-decoration: none;
        outline: 0;
        opacity: 1;
        span {
            display: none;
        }
    }
    .slider__control_left {
        position: absolute;
        top: -121px;
        right: 165px;
        width: 81px;
        height: 63px;
        background: $orangebuttoncolor;
        border-bottom: solid 7px $orangebuttonborder;
        box-shadow: $graybuttonshadow;
        border-top-left-radius: 45%;
        border-bottom-left-radius: 45%;
        span {
        font-family: GirloyExtraBold;
        font-size: 30px;
        text-align: center;
        margin-right: 5px;
        }
    }
    .slider__control_right {
        position: absolute;
        top: -121px;
        right: 79px;
        width: 81px;
        height: 63px;
        background: $orangebuttoncolor;
        border-bottom: solid 7px $orangebuttonborder;
        box-shadow: $orangebuttonshadow;
        border-top-right-radius: 45%;
        border-bottom-right-radius: 45%;
        span {
        margin-right: 35px;
        font-family: GirloyExtraBold;
        font-size: 30px;
        text-align: end;
        }
    }
    .slider__control::before {
        content: '';
        display: inline-block;
        width: 20px;
        height: 20px;
        background: transparent no-repeat center center;
        background-size: 100% 100%;
    }
}

